import Layout, { LayoutTheme } from '@/components/Layout';
import successIcon from '@/assets/success.svg';
import Logo from '@/components/Logo';
import { useParams } from '@jupiter/plugin-runtime/router';
import styles from './index.module.css';

const Success = () => {
  const { isSuccess } = useParams<{ isSuccess: string | undefined }>();
  return (
    <Layout>
      <div className={styles.success}>
        {Number(isSuccess) === 1 ? (
          <img
            src={successIcon}
            className={styles.successLogo}
            alt="success icon"
          />
        ) : null}
        <p className={styles.successText}>
          {Number(isSuccess) === 1
            ? '问卷提交成功，感谢你参加问卷调查'
            : `问卷提交失败请重试`}
        </p>
        <div className={styles.logo}>
          {/* <Logo theme={LayoutTheme.Light} />
           */}
          <p>
            分享问卷: <a href="https://modernjs.dev">modernjs.dev</a>
          </p>
        </div>
      </div>
    </Layout>
  );
};

export default Success;
